export async function openMCPEmbodiment() {
  PostHog.track('mcp_embodiment_click', { position: 'top right of builder inside dropdown' });
  const testDomain =
    workspace.serverData?.agent_domain && workspace.agent.id
      ? `${workspace.agent.id}.${workspace.serverData.agent_domain}`
      : '';

  const scheme = workspace.serverData.agent_domain.includes(':') ? 'http' : 'https';
  const prodDomain = workspace.agent.domain;

  if (!testDomain) {
    const content =
      '<div class="p-4">Agent domain is not set. Cannot provide MCP features</div>';
    openEmbodimentDialog(
      content,
      {},
      EMBODIMENT_DESCRIPTIONS.mcp.title,
      EMBODIMENT_DESCRIPTIONS.mcp.tooltipTitle,
    );
    return;
  }

  // Show loading state initially
  const initialContent = `
    <div class="emb-instructions p-4 flex-row">
       <div class="flex items-center gap-2 mb-4">
        <p class="text-gray-700">Enable MCP to access your agent through MCP integration.</p>
      </div>
      <div class="flex items-center justify-center h-32">
        <svg
          aria-hidden="true"
          class="inline text-gray-200 animate-spin dark:text-gray-600 fill-v2-blue w-12 h-12"
          viewBox="0 0 100 101"
          fill="none"
          xmlns="http://www.w3.org/2000/svg"
        >
          <path
            d="M100 50.5908C100 78.2051 77.6142 100.591 50 100.591C22.3858 100.591 0 78.2051 0 50.5908C0 22.9766 22.3858 0.59082 50 0.59082C77.6142 0.59082 100 22.9766 100 50.5908ZM9.08144 50.5908C9.08144 73.1895 27.4013 91.5094 50 91.5094C72.5987 91.5094 90.9186 73.1895 90.9186 50.5908C90.9186 27.9921 72.5987 9.67226 50 9.67226C27.4013 9.67226 9.08144 27.9921 9.08144 50.5908Z"
            fill="currentColor"
          />
          <path
            d="M93.9676 39.0409C96.393 38.4038 97.8624 35.9116 97.0079 33.5539C95.2932 28.8227 92.871 24.3692 89.8167 20.348C85.8452 15.1192 80.8826 10.7238 75.2124 7.41289C69.5422 4.10194 63.2754 1.94025 56.7698 1.05124C51.7666 0.367541 46.6976 0.446843 41.7345 1.27873C39.2613 1.69328 37.813 4.19778 38.4501 6.62326C39.0873 9.04874 41.5694 10.4717 44.0505 10.1071C47.8511 9.54855 51.7191 9.52689 55.5402 10.0491C60.8642 10.7766 65.9928 12.5457 70.6331 15.2552C75.2735 17.9648 79.3347 21.5619 82.5849 25.841C84.9175 28.9121 86.7997 32.2913 88.1811 35.8758C89.083 38.2158 91.5421 39.6781 93.9676 39.0409Z"
            fill="currentFill"
          />
        </svg>
      </div>
    </div>
  `;

  openEmbodimentDialog(
    initialContent,
    {},
    EMBODIMENT_DESCRIPTIONS.mcp.title,
    EMBODIMENT_DESCRIPTIONS.mcp.tooltipTitle,
  );

  // Function to fetch current MCP embodiment status
  const fetchMCPStatus = async () => {
    try {
      const response = await fetch(`/api/page/agents/agent/${workspace.agent.id}/settings`);
      const data = await response.json();
      const mcpEmbodiment = data.find((emb: any) => emb.key === 'mcp');
      try {
        const { isEnabled } = JSON.parse(mcpEmbodiment?.value);
        return isEnabled;
      } catch (error) {
        return false;
      }
    } catch (error) {
      console.error('Error fetching MCP status:', error);
      return false;
    }
  };

  // Function to update MCP embodiment status
  const updateMCPStatus = async (enabled: boolean) => {
    try {
      const response = await fetch(`/api/page/agents/agent/${workspace.agent.id}/settings`, {
        method: 'PUT',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({
          key: 'mcp',
          value: JSON.stringify({ isEnabled: enabled })
        }),
      });
      if (!response.ok) throw new Error('Failed to update MCP status');
      return true;
    } catch (error) {
      console.error('Error updating MCP status:', error);
      return false;
    }
  };

  // Get initial status
  const isEnabled = await fetchMCPStatus();
  const mcpDevUrl = `${scheme}://${testDomain}/mcp/sse`;
  const mcpProdUrl = `${scheme}://${prodDomain}/mcp/sse`;
  const content = `
    <div class="emb-instructions p-4 flex-row">
      <div class="flex items-center gap-2 mb-4">
        <p class="text-gray-700">Enable MCP to access your agent through MCP integration.</p>
      </div>
      <div class="space-y-4">
        <div class="flex items-center justify-between">
          <label class="text-sm font-medium text-gray-700">Enable MCP</label>
          <div class="relative inline-block w-12 align-middle select-none">
            <input type="checkbox" 
              id="mcp-toggle" 
              class="relative w-[35px] h-[20px] items-center bg-[#757575] border-transparent text-transparent rounded-3xl cursor-pointer transition-colors ease-in-out duration-200 disabled:opacity-50 disabled:pointer-events-none checked:bg-none checked:text-blue-600 checked:border-blue-600 checked:bg-blue-600 focus:ring-0 focus:ring-offset-0 focus:ring-offset-transparent before:inline-block before:w-4 before:h-4 before:bg-white checked:before:bg-blue-200 before:translate-x-0 checked:before:translate-x-full before:rounded-full before:shadow before:transform before:ring-0 before:transition before:ease-in-out before:duration-200 before:mt-[1px] smt-input-toggle text-gray-900 border border-gray-200 sm:text-md "
              ${isEnabled ? 'checked' : ''}
            />
            <div id="mcp-toggle-spinner" class="hidden absolute inset-0 flex items-center justify-center">
              <div class="w-4 h-4 border-2 border-white border-t-transparent rounded-full animate-spin"></div>
            </div>
          </div>
        </div>
        <div id="mcp-url-container" class="${isEnabled ? '' : 'hidden'}">
          <label class="block text-sm font-medium text-gray-700 mb-1">MCP Dev URL</label>
          <div class="flex items-center gap-2">
            <div type="text" 
              id="mcp-dev-url" 
              readonly 
              class="flex-1 px-3 py-2 border border-gray-300 rounded-md bg-gray-50 text-gray-700 break-all"
            >
              ${mcpDevUrl}
            </div>
            <button onclick="navigator.clipboard.writeText('${mcpDevUrl}')" 
              class="px-3 py-2 text-sm text-white bg-smyth-emerald-400 rounded-md hover:opacity-75 whitespace-nowrap flex-shrink-0">
              Copy
            </button>
          </div>
          <label class="mt-6 block text-sm font-medium text-gray-700 mb-1">MCP Prod URL</label>
          <div class="flex items-center gap-2">
            <div type="text" 
              id="mcp-prod-url" 
              readonly 
              class="flex-1 px-3 py-2 border border-gray-300 rounded-md bg-gray-50 text-gray-700 break-all" 
            > 
              ${mcpProdUrl}
            </div>
            <button onclick="navigator.clipboard.writeText('${mcpProdUrl}')" 
              class="px-3 py-2 text-sm text-white bg-smyth-emerald-400 rounded-md hover:opacity-75 whitespace-nowrap flex-shrink-0">
              Copy
            </button>
          </div>
        </div>
      </div>
    </div>
  `;

  const actions = {};
  openEmbodimentDialog(
    content,
    actions,
    EMBODIMENT_DESCRIPTIONS.mcp.title,
    EMBODIMENT_DESCRIPTIONS.mcp.tooltipTitle,
  );

  // Add event listener for toggle
  const toggle = document.getElementById('mcp-toggle') as HTMLInputElement;
  const urlContainer = document.getElementById('mcp-url-container');
  const toggleSpinner = document.getElementById('mcp-toggle-spinner');

  toggle?.addEventListener('change', async (e) => {
    const isChecked = (e.target as HTMLInputElement).checked;

    // Show spinner and disable toggle
    if (toggleSpinner) toggleSpinner.classList.remove('hidden');
    if (toggle) toggle.disabled = true;

    const success = await updateMCPStatus(isChecked);

    // Hide spinner and enable toggle
    if (toggleSpinner) toggleSpinner.classList.add('hidden');
    if (toggle) toggle.disabled = false;

    if (success) {
      if (urlContainer) {
        urlContainer.classList.toggle('hidden', !isChecked);
      }
      toast(isChecked ? 'MCP enabled' : 'MCP disabled', 'Info', 'success');
    } else {
      // Revert toggle state on failure
      (e.target as HTMLInputElement).checked = !isChecked;
      toast('Failed to update MCP status', 'Info', 'alert');
    }
  });
}

